.menu-bars {
	margin-left: 1rem;
	color: white;
	font-size: 2rem;
	background: none;
}
.menu-bars svg {
	font-size: 2rem;
}

.nav-menu {
	width: 250px;
	height: 100vh;
	background-color: var(--secondary);
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 850ms;
}
.nav-menu.active {
	left: 0;
	transition: 350ms;
}
.nav-menu-items {
	padding-left: 0px;
	width: 100%;
	list-style-type: none;
}
.nav-text {
	display: -webkit-flex;
	display: flex;
	margin-left: 0px;
	margin-right: 15px;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}
.nav-text a {
	text-decoration: none;
	color: white;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-align-self: flex-start;
	        align-self: flex-start;
	padding: 0 16px;
	border-radius: 4px;
}
.nav-text span {
	margin-left: 5px;
}
.nav-text button {
	background-color: transparent;
	color: white;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-align-self: flex-start;
	        align-self: flex-start;
	padding: 0 16px;
	border: none;
	border-radius: 4px;
}
.nav-text button:hover {
	background-color: var(--accent-pink);
	cursor: pointer;
}
.nav-text a:hover {
	background-color: var(--accent-pink);
}

.navbar-toggle {
	background-color: var(--secondary);
	margin: 7px 25px;
	text-align: start;
}
.sign-out {
	position: absolute;
	bottom: 0;
}
@media (max-width: 768px) {
	.menu-bars {
		margin-left: 1rem;
		font-size: 1.5rem;
	}
	
	.nav-menu {
		width: 100%;
	}
	.nav-text {
		padding: 0 10px;
		font-size: 12px;
	}
}

.appbar {
  z-index: 9;
  position: fixed;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  color: white;
  padding: 0px 5px;
  padding-right: 15px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: var(--secondary);
  box-shadow: black 0px 1px 6px;
}
.slider {
  z-index: 9;
  background-color: var(--secondary);
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.home {
  text-align: center;
  /* font-family: Roboto; */
  font-style: normal;
  padding: 10px;
  font-size: 30px;
  color: var(--primary);
  text-decoration: none;
}
.home b {
  /* font-family: Roboto; */
  font-size: 30px;
}
.home:hover {
  color: var(--accent-mustard);
}
.appbar-user {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 5px;
  margin: 0 10px;
}
.appbar-user p {
  font-size: 40;
}
@media (max-width: 768px) {
  .home {
    padding: 0;
  }
  .slider {
    height: 50px;
  }
  .appbar-user p {
    margin: 0;
  }
  .home {
    font-size: 20px;
  }
  .home b {
    font-size: 20px;
  }
}

